
"use client";

import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { cn } from "@/lib/utils";
import { Calendar as CalendarIcon } from "lucide-react";
import { format } from "date-fns";
import React from "react";
import type { Session, User } from "@/lib/types";
import { useToast } from "@/hooks/use-toast";
import { useUser, useFirebase } from "@/firebase";
import { collection, addDoc } from "firebase/firestore";
import { errorEmitter } from "@/firebase/error-emitter";
import { FirestorePermissionError } from "@/firebase/errors";

const sessionSchema = z.object({
  title: z.string().min(3, "Title must be at least 3 characters."),
  description: z.string(),
  date: z.date({ required_error: "A date is required." }),
  time: z
    .string()
    .regex(
      /^([0-1]?[0-9]|2[0-3]):[0-5][0-9]\s(AM|PM)$/,
      "Invalid time format (e.g., 02:00 PM)."
    ),
});

interface CreateSessionDialogProps {
  children: React.ReactNode;
  sessionToEdit?: Session;
  onSessionCreated?: (session: Session) => void;
  onSessionUpdated?: (session: Session) => void;
}

export default function CreateSessionDialog({
  children,
  sessionToEdit,
  onSessionCreated,
  onSessionUpdated,
}: CreateSessionDialogProps) {
  const [open, setOpen] = React.useState(false);
  const { toast } = useToast();
  const { user: firebaseUser } = useUser();
  const { firestore } = useFirebase();

  const form = useForm<z.infer<typeof sessionSchema>>({
    resolver: zodResolver(sessionSchema),
    defaultValues: {
      title: "",
      description: "",
      date: new Date(),
      time: "10:00 AM",
    },
  });

  const isEditing = !!sessionToEdit;

  React.useEffect(() => {
    if (open) {
      if (isEditing && sessionToEdit) {
        form.reset({
          title: sessionToEdit.title,
          description: sessionToEdit.description,
          date: new Date(sessionToEdit.date),
          time: sessionToEdit.time,
        });
      } else {
        form.reset({
          title: "",
          description: "",
          date: new Date(),
          time: "10:00 AM",
        });
      }
    }
  }, [sessionToEdit, form, open, isEditing]);

  async function onSubmit(values: z.infer<typeof sessionSchema>) {
    if (!firestore || !firebaseUser) return;
    const sessionData = {
      ...values,
      date: format(values.date, "yyyy-MM-dd"),
    };

    if (isEditing && sessionToEdit) {
      const updatedSession: Session = {
        ...sessionToEdit,
        ...sessionData,
        status:
          !!sessionToEdit.hostId && !!sessionToEdit.clientId
            ? "booked"
            : "available",
      };
      onSessionUpdated?.(updatedSession);
      toast({
        title: "Session Updated",
        description: "The session has been successfully updated.",
      });
    } else {
      const newSession: Omit<Session, 'id'> = {
        ...sessionData,
        hostId: firebaseUser?.uid || null,
        clientId: null,
        status: "available",
        messages: [],
        lastMessageReadBy: [],
        cancellationReadBy: [],
      };
      
      try {
        const sessionsCollection = collection(firestore, 'sessions');
        await addDoc(sessionsCollection, newSession);
         toast({
            title: "Session Created",
            description: "The new session has been added to the dashboard.",
        });
      } catch (error) {
        errorEmitter.emit('permission-error', new FirestorePermissionError({
            path: `sessions`,
            operation: 'create',
            requestResourceData: newSession
        }));
      }
    }
    setOpen(false);
  }

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>{children}</DialogTrigger>
      <DialogContent className="sm:max-w-2xl">
        <DialogHeader>
          <DialogTitle className="font-headline">
            {isEditing ? "Edit Session" : "Create New Session"}
          </DialogTitle>
          <DialogDescription>
            {isEditing
              ? "Update the details for this session."
              : "Fill in the details to create a new work session."}
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form
            onSubmit={form.handleSubmit(onSubmit)}
            className="grid grid-cols-1 md:grid-cols-2 gap-4 py-4"
          >
            <FormField
              control={form.control}
              name="title"
              render={({ field }) => (
                <FormItem className="md:col-span-2">
                  <FormLabel>Title</FormLabel>
                  <FormControl>
                    <Input {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="description"
              render={({ field }) => (
                <FormItem className="md:col-span-2">
                  <FormLabel>Description</FormLabel>
                  <FormControl>
                    <Textarea {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="date"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Date</FormLabel>
                  <Popover>
                    <PopoverTrigger asChild>
                      <FormControl>
                        <Button
                          variant={"outline"}
                          className={cn(
                            "pl-3 text-left font-normal w-full",
                            !field.value && "text-muted-foreground"
                          )}
                        >
                          {field.value ? (
                            format(field.value, "PPP")
                          ) : (
                            <span>Pick a date</span>
                          )}
                          <CalendarIcon className="ml-auto h-4 w-4 opacity-50" />
                        </Button>
                      </FormControl>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <Calendar
                        mode="single"
                        selected={field.value}
                        onSelect={field.onChange}
                        initialFocus
                      />
                    </PopoverContent>
                  </Popover>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="time"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Time</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., 02:00 PM" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <DialogFooter className="md:col-span-2 pt-4">
              <DialogClose asChild>
                <Button type="button" variant="outline">
                  Cancel
                </Button>
              </DialogClose>
              <Button type="submit">
                {isEditing ? "Save Changes" : "Create Session"}
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
